import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AuthGuardService } from '../auth0/auth-guard.service';

const AccountRouts: Routes = [
  {
    path: '',
    children: [
      {
        path: 'collection',
        loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('./collection/collection.module')['CollectionModule']);  });}),
        canActivate: [AuthGuardService],
      },
      {
        path: 'settings',
        loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('./account-settings/account-settings.module')['AccountSettingsModule']);  });}),
        canActivate: [AuthGuardService],
      },
      {
        path: 'inbox',
        loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('./messages/messages.module')['MessagesModule']);  });}),
        canActivate: [AuthGuardService],
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(AccountRouts)],
  exports: [RouterModule],
  declarations: [],
})
export class AccountRountingModule {}



